<template>
  <div
    class="d-flex flex-column"
    :class="{ 'justify-space-between': $vuetify.breakpoint.mobile }"
  >
    <div>
      <v-checkbox
        v-model="form.values.enable_fleet_mode"
        label="Enable Fleet Drive mode on Driver apps"
        messages="Fleet Drive mode controls and limits what the Drivers of the fleet are able to do via their Mobile app."
      >
        <template v-slot:message="{ message }">
          <span class="text-body-2 primary-grey--text custom-message">{{ message }}</span>
        </template>
      </v-checkbox>
      <v-checkbox
        v-model="form.values.hide_payment_information"
        label="Hide Payment Information on Driver apps (will show price for payment terms: COD, COP and CKOD)"
        class="text--primary"
        ripple
        messages="This works when Fleet Driver Mode is checked."
        :disabled="!form.values.enable_fleet_mode"
      >
        <template v-slot:message="{ message }">
          <span class="text-body-2 primary-grey--text custom-message">{{ message }}</span>
        </template>
      </v-checkbox>
      <v-checkbox
        v-model="form.values.disable_customer_not_available"
        label="Disable Customer not Available button on the Driver App"
      />
      <v-checkbox
        v-model="form.values.disable_sti"
        label="Disable STI (Subject to Inspection) button on the Driver App"
      />
    </div>
    <v-btn
      :width="$vuetify.breakpoint.mobile ? '100%' : 100"
      :height="$vuetify.breakpoint.mobile ? 50 : 35"
      color="primary"
      depressed
      class="mt-2 font-weight-bold"
      @click="submitSettingsDriverAppForm"
    >
      Save
    </v-btn>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import { actions } from '@modules/settings/constants';

const { mapState, mapActions } = createNamespacedHelpers('settings');

export default {
  name: 'SettingsDriverApp',
  data: () => ({
    form: {
      values: {
        disable_customer_not_available: null,
        disable_sti: null,
        enable_fleet_mode: null,
        hide_payment_information: null,
      },
    },
  }),
  computed: {
    ...mapState({
      driverApp: (state) => state.driverAppSettings.data,
    }),
  },
  beforeMount() {
    setTimeout(() => {
      this.form.values = {
        disable_customer_not_available: this.driverApp.disable_customer_not_available,
        disable_sti: this.driverApp.disable_sti,
        enable_fleet_mode: this.driverApp.enable_fleet_mode,
        hide_payment_information: this.driverApp.hide_payment_information,
      };
    }, 1);
  },
  methods: {
    ...mapActions({
      addSettingsDriverApp: actions.ADD_DRIVERAPP_SETTINGS,
    }),
    submitSettingsDriverAppForm() {
      this.addSettingsDriverApp({ data: this.form.values }).then(() => {
        this.$notificationSuccess('Settings was saved');
      })
        .catch(() => {
          this.$notificationError('Settings wasn\'t saved');
        });
    },
  },
};
</script>

<style scoped lang="scss">
  .custom-message {
    display: flex;
    position: relative;
    transform: translateY(-8px);
  }
</style>
